.transcript {
  width: 100%;
  margin: auto;
  font-family: Arial, sans-serif;
}

.transcript-body {
  width: 100%;
  height: 200px;
  overflow-y: scroll;
}

.transcript-line {
  position: relative;
  padding: 5px 0px 5px 6%;
  cursor: pointer;
  line-height: 1.3;
}

.transcript-timestamp {
  display: inline-block;
  position: absolute;
}

.transcript-text {
  display: block;
  margin-left: 51px;
  margin-right: 10px;
}

.transcript-text:empty {
  height: 20px;
}

.transcript-line:hover {
  background-color: $transcript-hover-background-color;
}

.transcript-line.is-active {
  font-weight: bold;
  background-color: $transcript-active-line-background-color;
  color: $black;
  box-shadow: inset 4px 0 0 0 $transcript-active-line-border-color;
}
