@mixin icon-styles {
  font-weight: normal;
  font-style: normal;
  font-size: $material-icon-font-normal;
  display: inline-block;
  line-height: 1;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;

  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;

  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;

  /* Support for IE. */
  font-feature-settings: "liga";
}

@mixin line-clamp($line-height, $max-lines, $padding: 0) {
  position: relative;
  max-height: calc((#{$line-height} * #{$max-lines}) + #{$padding});
  overflow: hidden;
}
